2px #8229c6;margin:0.2rem;width:90px;padding:5px 0;border-radius:5px}.product-list-box .product-list-container .product-list-item_modifications{position:absolute;width:100%;bottom:0;border-bottom-left-radius:5px;border-bottom-right-radius:5px;background-color:#8229c6;text-align:justify;height:30px;margin-left:-20px;display:flex;justify-content:space-between;align-items:center;color:white}.product-list-box .product-list-container .product-list-item_modifications>button{width:50%;height:30px;margin:0;padding:0;font-size:1rem;border-bottom-left-radius:5px;border-bottom-right-radius:5px}.product-list-box .product-list-container .product-list-item_diverse{display:flex;flex-direction:column;text-align:left;margin-top:1rem;margin-bottom:1.5rem}

