@import 'src/_variables.scss';

.product-list-box {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 3rem;

  @media (max-width: 900px) {
    padding: 0;
  }

  & .product-list-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 0.5rem;
    margin-bottom: 3rem;

    @media (max-width: 900px) {
      margin-top: 1rem;
    }

    & .product-list-item {
      min-height: 350px;
      width: 350px;
      padding: 20px;
      border-radius: 5px;
      margin-top: 2.5rem;
      margin-left: 2.5rem;
      position: relative;
      box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
      background-color: white;

      @media (max-width: 900px) {
        width: 70%;
        margin-left: 0;
        margin-top: 4.5rem;
      }

      &_img {
        height: 100px;
        width: 100px;
        position: absolute;
        top: -25px;
        left: -25px;
        border-radius: 50%;
        box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: $secondary-color-alt;

        & > img {
          width: 100px;
        }

        @media (max-width: 900px) {
          top: -60px;
          left: 90px;
        }
      }

      &_title {
        text-align: center;
        font-size: 1.2rem;
        margin-left: 3rem;
        color: $secondary-color;

        @media (max-width: 900px) {
          text-align: left;
          margin: 0.4em;
        }
      }

      &_delete {
        position: absolute;
        top: 0;
        right: 0;
        margin: 0.5rem;
        color: red;
        cursor: pointer;
        border-radius: 50%;
      }

      &_description {
        margin-top: 1.5rem;
        text-align: justify;
        min-height: 57px;
      }

      &_infos {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 0.5rem;

        & span {
          text-align: center;
          border: solid 2px $main-color-alt;
          margin: 0.2rem;
          width: 90px;
          padding: 5px 0;
          border-radius: 5px;
        }
      }

      &_modifications {
        position: absolute;
        width: 100%;
        bottom: 0;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
        background-color: $main-color-alt;
        text-align: justify;
        height: 30px;
        margin-left: -20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: white;

        & > button {
          width: 50%;
          height: 30px;
          margin: 0;
          padding: 0;
          font-size: 1rem;
          border-bottom-left-radius: 5px;
          border-bottom-right-radius: 5px;
        }
      }

      &_diverse {
        display: flex;
        flex-direction: column;
        text-align: left;
        margin-top: 1rem;
        margin-bottom: 1.5rem;
      }

      @media (max-width: 900px) {
      }
    }
  }
}
